@import url(https://fonts.googleapis.com/css?family=Bitter);
*,
html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: -webkit-linear-gradient(60deg, #d64161 20%, #6b5b95 80%);
  background-image: linear-gradient(30deg, #d64161 20%, #6b5b95 80%);
  display: flex;
  font-family: 'Bitter', serif;
  height: 100vh;
  justify-content: center;
  padding-top: 10%;
  width: 100vw;
}

#root {
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  max-height: 90vh;
  min-height: 60vh;
  padding: 10px 0 20px 0;
  width: 80vw;
}

input[type='text'] {
  align-self: top;
  background-color: rgba(255, 255, 255, 0.5);
  border: none;
  color: #3f3558;
  font-family: 'Bitter', serif;
  font-size: 1em;
  height: 100%;
  justify-self: center;
  padding: 0 0.5em;
  text-align: center;
  width: 100%;
}

input:focus {
  outline: none;
  box-shadow: 0 0 1px 1px #c4b6e6;
}

.wrapper-tasks {
  /*max-height: 300px;*/
  min-height: 10px;
  overflow-y: scroll;
}

ul {
  list-style-type: none;
}

.todo-row {
  align-items: flex-start;
  border-bottom: 0.1em dotted rgba(255, 255, 255, 0.3);
  display: flex;
  padding: 10px;
}

.todo-row:last-child {
  align-items: flex-start;
  display: flex;
  border-bottom: none;
  padding: 10px;
}

.todo-row:hover {
  background-color: rgba(255, 255, 255, 0.3);
}

li {
  font-size: 1.3em;
  margin-left: 15px;
}

.todo {
  color: whitesmoke;
}

.done {
  color: #6b5b95;
  text-decoration: line-through;
}

.wrapper-counter {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding: 0 20px;
}

.wrapper-counter {
  grid-row: 3;
  align-self: flex-end;
}

.Counter {
  color: white;
  display: inline;
  font-family: 'Bitter', 400i, serif;
  font-size: 0.9em;
}

button {
  background: rgba(0, 0, 0, 0);
  border: none;
  color: white;
  font-size: 0.8em;
  height: 1.5em;
  margin-left: auto;
  margin-right: 15px;
  width: 1.5em;
}

button:focus {
  outline: none;
}

.wrapper-separator {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
}

.hr {
  width: 100%;
  border-bottom: 5px solid;
  color: rgba(255, 255, 255, 0.3);
}

.Separator {
  font-size: 1.4em;
  font-weight: 600;
  color: #6b5b95;
  white-space: nowrap;
  align-items: center;
  margin: 0 10px;
  letter-spacing: 0.05em;
}

app > * {
  align-items: center;
  display: flex;
  justify-content: center;
}

.App {
  display: grid;
  grid-template-columns: 1fr;
  min-width: 100%;
  grid-template-rows: 80px 40px 35px 1fr;
}

h1 {
  align-self: center;
  color: #6b5b95;
  font-family: 'Bitter', serif;
  font-size: 3.4em;
  text-align: center;
}

