.wrapper-separator {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
}

.hr {
  width: 100%;
  border-bottom: 5px solid;
  color: rgba(255, 255, 255, 0.3);
}

.Separator {
  font-size: 1.4em;
  font-weight: 600;
  color: #6b5b95;
  white-space: nowrap;
  align-items: center;
  margin: 0 10px;
  letter-spacing: 0.05em;
}
