.wrapper-tasks {
  /*max-height: 300px;*/
  min-height: 10px;
  overflow-y: scroll;
}

ul {
  list-style-type: none;
}

.todo-row {
  align-items: flex-start;
  border-bottom: 0.1em dotted rgba(255, 255, 255, 0.3);
  display: flex;
  padding: 10px;
}

.todo-row:last-child {
  align-items: flex-start;
  display: flex;
  border-bottom: none;
  padding: 10px;
}

.todo-row:hover {
  background-color: rgba(255, 255, 255, 0.3);
}

li {
  font-size: 1.3em;
  margin-left: 15px;
}

.todo {
  color: whitesmoke;
}

.done {
  color: #6b5b95;
  text-decoration: line-through;
}
