input[type='text'] {
  align-self: top;
  background-color: rgba(255, 255, 255, 0.5);
  border: none;
  color: #3f3558;
  font-family: 'Bitter', serif;
  font-size: 1em;
  height: 100%;
  justify-self: center;
  padding: 0 0.5em;
  text-align: center;
  width: 100%;
}

input:focus {
  outline: none;
  box-shadow: 0 0 1px 1px #c4b6e6;
}
