app > * {
  align-items: center;
  display: flex;
  justify-content: center;
}

.App {
  display: grid;
  grid-template-columns: 1fr;
  min-width: 100%;
  grid-template-rows: 80px 40px 35px 1fr;
}

h1 {
  align-self: center;
  color: #6b5b95;
  font-family: 'Bitter', serif;
  font-size: 3.4em;
  text-align: center;
}
